import React, { useState } from "react";
import Modal from "react-modal";
import { useDispatch } from "react-redux";

import { Container } from "./style";
import { colors } from "../../../../assets/colors";


const ModalAddUser = (props) => {

    const [nome, setNome] = useState("")
    const [email, setEmail] = useState("")
    const [senha, setSenha] = useState("")
    const [repetirSenha, setRepetirsenha] = useState("")
    const dispatch = useDispatch()

    function addUser() {
        if (nome === '' || email === "" || senha === "" || repetirSenha === "") {
            alert("Preencha todos os dados")
        } else if (senha !== repetirSenha) {
            alert("As senhas não correspondem")
        } else {
            dispatch({
                type: "SAGA_ADD_USER",
                payload: {
                    nome: nome,
                    email: email,
                    senha: senha,
                    perfil_id: 1
                }
            })
            props.visible(false)
        }
    }

    return (
        <Modal isOpen={true} style={{
            content: {
                backgroundColor: colors.primaryColor,
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
            }
        }}>
            <Container>
                <p>CADASTRO DE USUÁRIO</p>
                <input
                    placeholder={"Nome:"}
                    value={nome}
                    onChange={(e) => setNome(e.target.value)}
                />
                <input
                    placeholder={"E-mail:"}
                    type={"email"}
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                <input
                    placeholder={"Senha:"}
                    type={"password"}
                    value={senha}
                    onChange={(e) => setSenha(e.target.value)}
                />
                <input
                    placeholder={"Repetir senha:"}
                    type={"password"}
                    value={repetirSenha}
                    onChange={(e) => setRepetirsenha(e.target.value)}
                />

                <select name="cars" id="cars">
                    <optgroup label="Perfil">
                        <option value="Usuário">Volvo</option>
                        <option value="Parceiro">Saab</option>
                        <option value="Administrador">Saab</option>
                    </optgroup>
                </select>

                <button onClick={() => { addUser() }} >SALVAR</button>
                <button onClick={() => { props.visible(false) }}>SAIR</button>
            </Container>
        </Modal>
    )
}

export default ModalAddUser;