import React, { } from "react";
import { FiEdit, FiXCircle } from "react-icons/fi";


const ItemEditUser = (props) => {
console.log(props)
    return (
        <tr>
            <td>{props.user.id}</td>
            <td>{props.user.nome}</td>
            <td>{props.user.email}</td>
            <td>
                <div><FiEdit onClick={() => {props.userEdit(props.user); props.visibleModalEdit(true) }} /></div>
                <div><FiXCircle onClick={() => {props.visibleModalDelete(true) }} /></div>
            </td>
        </tr>
    )
}

export default ItemEditUser;