
import { BrowserRouter, Switch, Route } from "react-router-dom";

import DashboardPage from "./pages/dashboard";
import UserPage from "./pages/user";
import EventoPage from "./pages/evento";
import CategoriaPage from "./pages/categoria";
import PerfilPage from "./pages/perfil";
import EquipePage from "./pages/equipe";
import LoginPage from "./pages/login";

const Routes = () => {

    return(
        <BrowserRouter>
            <Switch>
                <Route exact path={"/"} component={DashboardPage} />
                <Route exact path={"/users"} component={UserPage} />
                <Route exact path={"/eventos"} component={EventoPage} />
                <Route exact path={"/categorias"} component={CategoriaPage} />
                <Route exact path={"/perfis"} component={PerfilPage} />
                <Route exact path={"/equipe"} component={EquipePage} />
                <Route exact path={"/login"} component={LoginPage} />
            </Switch>
        </BrowserRouter>
    )
}

export default Routes;