import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { BoxLogin, Container,  BtnLogin } from "./style";

import { IoIosMail, IoMdLock, IoMdPin } from "react-icons/io";

const Login = () => {

    const dispatch = useDispatch();
    const userLogado = useSelector(state => state.loginReducer)
    const history = useHistory();

    const [email, setEmail] = useState('');
    const [senha, setSenha] = useState('');

    function validarDados() {
        if (email === "" || senha === "") {
            alert("Favor preencher todos os dados!")
        } else {
            dispatch({
                type: "SAGA_SET_LOGIN",
                payload: {
                    email: email,
                    senha: senha
                }
            })
        }
    }

    useEffect(() => {
        //console.log(userLogado.length);
        if (userLogado.length !== 0) {
            history.push("/");
        }

    }, [userLogado])

    return (
        <Container>
            <BoxLogin >
                <div>
                    <IoMdPin size={100} color={"white"} />
                </div>
                <p> ÁREA ADMINISTRATIVA </p>
                <div>
                    <span><IoIosMail size={30} color={"#555555"} /></span>
                    <input placeholder={"E-mail"} type={"email"} value={email} onChange={e => setEmail(e.target.value)} />
                </div>
                <div>
                    <span><IoMdLock size={30} color={"#555555"} /></span>
                    <input placeholder={"Senha"} type={"password"} value={senha} onChange={e => setSenha(e.target.value)} />
                </div>
                <BtnLogin onClick={() => { validarDados() }} >ENTRAR</BtnLogin>
            </BoxLogin>
        </Container>
    )
}

export default Login;