import styled from "styled-components";
import { colors } from "../../assets/colors";

export const Container = styled.div`
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
    background-color: ${colors.primaryColor};
`;

export const BoxLogin = styled.div`
    display: flex;
    width: 300px;
    margin-top: -100px;
    flex-direction: column;
    border-radius: 10px;

    div{
        background-color: #fff;
        padding:2.5px;
        border-radius: 5px;
        display: flex;
        flex-direction: row;
        margin:5px;
        justify-content: center;
        align-items: center;
        span{
            background-color: #EEEEEE;
            padding-left:5px;
            padding-right:5px;
            align-items: center;
            justify-content: center;
        }
       
    }

    div:first-child{
        background-color: transparent !important;
    }

    input{
        display:flex;
        width: 250px;
        height: 30px;
        padding-left:10px;
        border: 0px solid;
    }

    p{
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;
        font-size: 22px;

    }
    

`;



export const BtnLogin = styled.a`
    display: flex;
    background-color: #5CB85C;
    height: 40px;
    margin:5px;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    color: white;
    border: 1px solid;

    &:hover {background: ${colors.primaryColor} ; cursor: pointer; border: 1px solid}

`;