import styled from "styled-components";
import { colors } from "../../assets/colors";

export const Container = styled.div`
    background-color: #212529;
    height: 100vh;
    display: flex;
    flex-direction: column;
    width: 200px;
    align-items: center;
`;

export const BoxLogo = styled.div`
    display: flex;
    width: 180px;
    height: 80px;
    border-radius: 10px;
    margin: 10px;
    justify-content: center;
    align-items: center;
`;

export const Opc = styled.a`
    color: white;
    display: flex;
    margin:5px;
    font-size: 22px;
    align-items: center;
    border-radius: 10px;
    padding:5px;
    &:hover {background: ${colors.primaryColor}; cursor: pointer;}

    i{
        margin-left: 10px;
    }

`;

export const Menu = styled.div`
    display: flex;
    flex-direction: column;
    width: 175px;
    border-radius: 10px;
    padding: 10px;
    margin-top: 25px;
`;