import React, { useEffect, useState } from "react";
import { Container, Pages, } from "./style";

import {  FiPlus, } from "react-icons/fi";
import { IoMdArrowDropleft, IoMdArrowDropright } from "react-icons/io";
import { useDispatch, useSelector } from "react-redux";

import ItemUser from "../itemUser";
import ModalAddUser from "../modalAddUser";
import ModalEditUser from "../modalEditUser";
import ModalDeleteUser from "../modalDeleteUser";

const Grade = () => {

    let users = []
    const dispatch = useDispatch();
    users = useSelector(state => state.userReducer);

    const [visibleModalDelete, setVisibleModalDelete] = useState(false)
    const [visibleModalAdd, setVisibleModalAdd] = useState(false)
    const [visibleModalEdit, setVisibleModalEdit] = useState(false)
    const [userEdit, setUserEdit] = useState(false)

    useEffect(() => {
        dispatch({
            type: "SAGA_SET_USERS"
        })
    }, [])

    function dados() {
        if (users === false) {
            return <span>Erro</span>
        } else if (users.length === 0) {
            return <span>Carregando</span>
        } else {
            return (
                users.data.data.map((item) => (
                    <ItemUser
                        visibleModalEdit={setVisibleModalEdit}
                        visibleModalDelete={setVisibleModalDelete}
                        userEdit= {setUserEdit}
                        user={item}
                        key={"" + item.id} />
                ))
            )
        }
    }

    function pages() {
        if (users === false) { 
            return <span>Erro</span>
        } else if (users.length === 0) {
            return <span>Carregando</span>
        } else {
            console.log(users.data.data)
            return (
                <Pages>
                    <div>
                        <IoMdArrowDropleft size={30} onClick={() => {
                            users.data.prev_page_url != null && dispatch({ type: "SAGA_SET_USERS", payload: users.data.prev_page_url })
                        }} />
                    </div>
                    <span>
                        {
                            users.data.to + ' de ' + users.data.total
                        }
                    </span>
                    <div>
                        <IoMdArrowDropright size={30} onClick={() => {
                            users.data.next_page_url != null && dispatch({ type: "SAGA_SET_USERS", payload: users.data.next_page_url })
                        }} />
                    </div>
                </Pages>
            )
        }
    }

    return (
        <Container>
            {visibleModalDelete && <ModalDeleteUser visible={setVisibleModalDelete} />}
            {visibleModalAdd && <ModalAddUser visible={setVisibleModalAdd} />}
            {visibleModalEdit && <ModalEditUser visible={setVisibleModalEdit} user={userEdit} />}
            <table >
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>Nome</th>
                        <th>E-mail</th>
                        <th>
                            <div><FiPlus onClick={() => { setVisibleModalAdd(true) }} /></div>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {dados()}
                </tbody>
            </table>
            {pages()}
        </Container >

    )
}

export default Grade;