import styled from "styled-components";


export const Container = styled.div`
    height: 50px;
    display: flex;
    flex-direction: row;
    margin:10px;
    justify-content: space-between;
    align-items: center;
    padding-left:10px;
    padding-right:10px;
    font-weight: bold;
    font-style: italic;
    font-size: 22px;
`;

export const Titulo = styled.a`
    display: flex;
    width: 100px;
 
`;

export const Filtros = styled.input`
    display: flex;
    width: 100px;

`;

export const Pesquisa = styled.input`
    display: flex;
    width: 150px;

`;