import React, { useState } from "react";
import Modal from "react-modal";
import { useDispatch } from "react-redux";

import { Container } from "./style";
import { colors } from "../../../../assets/colors";

const ModalEditUser = (props) => {

    console.log(props)

    const [nome, setNome] = useState(props.user.nome)
    const [email, setEmail] = useState(props.user.email)
    const [senha, setSenha] = useState("")
    const dispatch = useDispatch()

    function editUser() {
        if (nome === '' || email === "") {
            alert("Nome e E-mail são obrigatorios")
        } else if (senha === '') {
            dispatch({
                type: "SAGA_EDIT_USER",
                payload: {
                    nome: nome,
                    email: email,
                    perfil_id: 1,
                    id: props.user.id 
                }
            })
            props.visible(false)
        }else {
            dispatch({
                type: "SAGA_EDIT_USER",
                payload: {
                    nome: nome,
                    email: email,
                    senha: senha,
                    perfil_id: 1,
                    id: props.user.id
                }
            })
            props.visible(false)
        }
    }

    return (
        <Modal isOpen={true} style={{
            content:{
                backgroundColor: colors.primaryColor,
                display:"flex",
                justifyContent: "center",
                alignItems: "center",
            }
        }}>
            <Container>
                <input
                    placeholder={"Nome:"}
                    value={nome}
                    onChange={(e) => setNome(e.target.value)}
                />
                <input
                    placeholder={"E-mail:"}
                    type={"email"}
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                />
                <input
                    placeholder={"Senha:"}
                    type={"password"}
                    value={senha}
                    onChange={(e) => setSenha(e.target.value)}
                />
                <button onClick={() => { editUser() }} >SALVAR</button>
                <button onClick={() => { props.visible(false) }}>SAIR</button>
            </Container>
        </Modal>
    )
}

export default ModalEditUser;