import styled from "styled-components";

import { colors } from "../../../../assets/colors";

export const Container = styled.div`
    display: flex;
    flex-direction: column;
    width: 350px;

    input{
        margin-bottom: 5px;
        height: 30px;
        padding-left:10px;
        border-radius: 5px;
    }
    button{
        height: 35px;
        margin-bottom: 5px;
        background-color: ${colors.primaryColor};
        color: white;
        font-size: 22px;
        border-radius: 5px;

        &:hover {background: ${colors.primaryColor} ; cursor: pointer; border: 1px solid}
    }
    p{
        display: flex;
        font-size: 25px;
        font-weight: bold;
        font-style: italic;
        justify-content: center;
        align-items: center;
        color: white;
    }
`;