import Axios from "axios"


export const login = ({ email, senha }) => (
    Axios.post("http://tour.ramersonrodrigues.com/api/usuario/login", {
        "email": email,
        "senha": senha,
    })
        .then(res => {
            //console.log(res)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res
            }
        }).catch(error => {
            //console.log(error)
            return false
        })
)

export const getUsers = (url = 'http://tour.ramersonrodrigues.com/api/usuario') => (
    Axios.get(url)
        .then(res => {
            //console.log(res.data);
            return res
        }).catch(error => {
            //console.log(error)
            return false
        })
)

export const addUser = (user) => (
    Axios.post("http://tour.ramersonrodrigues.com/api/usuario", {
        "nome": user.nome,
        "email": user.email,
        "senha": user.senha,
        "perfil_id": user.perfil_id
    })
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error) 
            return false
        })
)

export const editUser = (user) => (
    Axios.post("http://tour.ramersonrodrigues.com/api/usuario/" + user.id, user)
        .then(res => {
            console.log(res.data)
            if (typeof res.data.id === "undefined") {
                return false
            } else {
                return res.data
            }
        }).catch(error => {
            console.log(error)
            return false
        })
)

