import { BoxLogo, Container, Opc, Menu } from "./style";
import { useHistory } from "react-router-dom";
import { FiBarChart2, FiUser, FiLock, FiGrid, FiNavigation, FiCode } from "react-icons/fi";

import { IoMdPin } from "react-icons/io";

const Bar = () => {

    const history = useHistory();

    return (
        <Container>
            <BoxLogo>
                <IoMdPin size={50} color={"white"} />
            </BoxLogo>
            <Menu>
                <Opc onClick={() => { history.push("/") }}><FiBarChart2 /> <i>Dashboard</i></Opc>
                <Opc onClick={() => { history.push("/users") }}> <FiUser /> <i>Usuários</i></Opc>
                <Opc onClick={() => { history.push("/perfis") }}><FiLock /><i>Perfis</i></Opc>
                <Opc onClick={() => { history.push("/categorias") }}><FiGrid /> <i>Categorias</i></Opc>
                <Opc onClick={() => { history.push("/eventos") }}><FiNavigation /><i>Eventos</i></Opc>
                <Opc onClick={() => { history.push("/equipe") }}><FiCode /> <i>Equipe</i></Opc>
            </Menu>
        </Container >
    )
}

export default Bar;