import { Container, Page, Box } from "./style";
import Bar from "../../components/bar";
import Header from "../../components/header";
import Footer from "../../components/footer";


import BarTitulo from "./components/bar";
import Grade from "./components/grade";
 
const User = () => {

    return (
        <>
            <Container>
                <Bar />
                <Box>
                    <Header />
                    <Page>
                        <BarTitulo></BarTitulo>
                        <Grade></Grade>
                    </Page>
                    <Footer />
                </Box>

            </Container>

        </>
    )
} 

export default User;