import { call, put } from "redux-saga/effects";
import { login, getUsers, addUser, editUser } from "../../api/userService";

export function* sagaLogin(action) {
    const user = yield call(login, action.payload);
    if (user !== false) {
        //console.log(user);
        yield put({
            type: "SET_LOGIN",
            payload: user.data
        })

    } else {
        alert("Login ou senha incorrretos")
    }
}

export function* sagaUsers(action) {
    //console.log("teste")
    const users = yield call(getUsers, action.payload);
    //console.log(users);
    yield put({
        type: "SET_USERS",
        payload: users
    })
}

export function* sagaAddUser(action) {
    //console.log(action)
    const user = yield call(addUser, action.payload);
    //console.log("fim");
}


export function* sagaEditUser(action) {

    const user = yield call(editUser, action.payload);
    const users = yield call(getUsers);
    yield put({
        type: "SET_USERS",
        payload: users
    })
}

